import banner from '../../components/index/banner.vue'
import indextitle from '../../components/common/indextitle.vue'
import supermodel from '../../components/common/supermodel.vue'
import guessmodel from '../../components/common/guessmodel.vue'
export default {
	name: "",
	components: {
		banner,
		indextitle,
		supermodel,
		guessmodel
	},
	props: {},
	data() {
		return {
			// 接收请求到的轮播图数据
			bannerArr: [],
			// 接收请求到的热门超英的数据
			superArr: [],
			// 接收请求到的热门预告的数据
			noticeArr: [],
			// 接收请求到的猜你喜欢的数据
			guessArr: []
		}
	},
	methods: {
		// 获取首页轮播图数据
		getBanners() {
			uni.showLoading({
				title: '加载中'
			})
			uni.request({
				url: this.$api.getBanner(),
				method: 'POST',
				data: {
					type: 'superhero'
				},
				success: res => {
					this.bannerArr = res.data.data
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			});
		},
		// 请求热门超英的数据
		getSuper(type) {
			uni.request({
				url: this.$api.getHot(),
				method: 'POST',
				data: {
					type: type
				},
				header: {
					"content-type": 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (type === 'superhero') {
						this.superArr = res.data.data
					} else {
						this.noticeArr = res.data.data
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 获取猜你喜欢的数据
		getLikes() {
			uni.request({
				url: this.$api.getLike(),
				method: 'POST',
				data: {},
				success: res => {
					this.guessArr = res.data.data
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 播放按钮
		startPlay(ids) {
			this.noticeArr.map(item => {
				if (item.id != ids) {
					uni.createVideoContext(item.id).pause()
				}
			})

		}
	},
	mounted() {
		this.getBanners()
		this.getSuper('superhero')
		this.getSuper('trailer')
		this.getLikes()
	},
	onLoad() {

	},
	onPullDownRefresh() {
		this.getLikes()
		setTimeout(() => {
			uni.showToast({
				title: '刷新成功'
			});
			uni.stopPullDownRefresh();
		}, 1200)
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
